.container {
  display: flex;
  height: 168px;
  width: 168px;
  margin: 30px auto;
  border-radius: 50%;
  background-color: #e6e6e6;
}

.commonResult {
  margin: 30px 0;
  text-align: center;
}

.control {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.button {
  font-size: 16px;
}

.chartContainer {
  display: flex;
  justify-content: space-evenly;

  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.chart {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chartName {
  text-align: center;
  text-transform: capitalize;
}
