.balanceHistory {
  table {
    border-collapse: collapse;
    font-size: 14px;
    margin-bottom: 24px;
    width: 100%;
    text-align: left;

    tr:nth-of-type(odd) {
      background-color: #f9f9f9;
    }

    th,
    td {
      padding: 8px;
    }

    td {
      border-top: 1px solid #ddd;
    }
  }
}

.amount {
  white-space: nowrap;
}
