.profileInformation {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid rgba(190, 190, 190, 0.25);
  padding-bottom: 20px;

  @media (max-width: 480px) {
    flex-direction: column;

    .imgContainer {
      order: 0;
      margin-left: 0;
    }
  }
}

.timezone {
  display: flex;
  align-items: center;

  > div {
    margin-left: 5px;
  }
}

.links {
  display: flex;

  > div {
    margin-left: 5px;
  }

  .modals {
    margin-top: 4px;

    a {
      border-bottom: 1px dotted;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

.changePassword {
  width: 400px;
  min-width: auto;

  .container {
    > div {
      margin-bottom: 16px;
    }
  }

  .generatePassword {
    color: #00adcb;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
    align-items: center;

    button {
      font-size: 16px;
      width: 80%;
      margin-bottom: 16px;
    }
  }
}

.techRecommendation {
  li {
    margin-bottom: 16px;
    color: #4f4f4f;
  }

  .footer {
    text-align: right;
    margin-bottom: 16px;
  }
}

.imgContainer {
  display: block;
  min-width: 96px;
  height: 96px;
  float: right;
  position: relative;
  background: #fff;
  border: 0;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;

  @media (max-width: 992px) {
    margin-right: 0;
    margin-left: 15px;
    order: 1;
  }
}

.img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.name {
  font-size: 18px;
  font-weight: normal;
  margin: 10px 0 15px;
}

.line {
  margin-bottom: 10px;
}
