$success: #52d699;
$warning: #fabc29;
$danger: #ff596e;

.circleProgress {
  padding: 10px;
  display: flex;
  justify-content: center;

  circle {
    stroke-width: 8px;
    transform: scale(0.9) rotate(-90deg);
    transform-origin: center;
  }
}

.wrap {
  display: inline-block;
  position: relative;
}

.background {
  stroke: #ddd;

  &.success {
    stroke: $success;
  }

  &.warning {
    stroke: $warning;
  }

  &.danger {
    stroke: $danger;
  }
}

.bar {
  stroke: #1e90ff;
  transition: stroke-dashoffset 0.5s linear;

  &.success {
    fill: $success;
    stroke: $success;
  }

  &.warning {
    fill: $warning;
    stroke: $warning;
  }

  &.danger {
    fill: $danger;
    stroke: $danger;
  }
}

.text {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  font-weight: bold;
  justify-content: center;
  font-size: 15px;
  color: #4f4f4f;
  text-transform: capitalize;
}
