.accountHealth {
  display: inline;

  & > svg {
    width: 13px;
    height: 13px;
    margin-right: 3px;
  }
}

.accountHealthTooltip {
  h4 {
    margin: 8px 0 4px;
    font-weight: bold;
  }

  ul {
    margin: 0;
    padding: 0 0 0 16px;
    text-align: left;

    > li {
      & + li {
        margin: 4px 0 0;
      }
    }
  }

  svg {
    margin: 0 3px;
  }
}
