@import 'styles/variables.scss';

.changePassword {
  width: 400px;
  min-width: auto;

  form > * {
    margin-bottom: 16px;
  }

  :global {
    .MuiTextField-root {
      margin-bottom: 16px;

      &.hasError {
        .MuiFormHelperText-root {
          color: $errorColor;
          margin-left: 0;
        }
      }
    }
  }

  .generatePassword {
    color: #00adcb;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      text-decoration: underline;
    }
  }

  .footer {
    display: flex;
    flex-direction: column;
    align-items: center;

    button {
      font-size: 16px;
      width: 80%;
      margin-bottom: 16px;
    }
  }
}
